/*    */
.course-table-container {
  width: 90%;
  margin-top: 150px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

h2 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 24px;
  color: #333;
}

.course-table {
  width: 100%;
  border-collapse: collapse;
  table-layout: fixed;
}

.course-table th,
.course-table td {
  padding: 12px;
  text-align: center;
  border: 1px solid #ddd;
}

.course-table th {
  background-color: #4caf50;
  color: white;
  font-size: 16px;
}

.course-table tr:nth-child(even) {
  background-color: #f2f2f2;
}

/* tr hover  */
.course-table tr:hover {
  background-color: #e0f7fa; /*  tr     */
}

.course-table td {
  font-size: 14px;
  color: #555;
}

.no-data {
  text-align: center;
  color: #999;
  font-style: italic;
  font-size: 16px;
}

/*   */
.course-table td:nth-child(4),
.course-table td:nth-child(5) {
  font-size: 14px;
  color: #555;
}

/*     */
.course-table td:nth-child(11),
.course-table td:nth-child(12) {
  font-weight: bold;
  color: #2e8b57;
}

/*   courseName      */
@media (max-width: 768px) {
  .course-table th,
  .course-table td {
    font-size: 12px; /*    */
    padding: 8px; /*     */
  }

  /*   */
  .course-table thead {
    display: none;
  }

  /*  td  */
  .course-table td {
    display: none;
  }

  /*     */
  .course-table td:nth-child(1) {
    display: block;
    font-weight: bold;
  }

  .course-table td:nth-child(1)::after {
    content: " : ";
  }

  /* No data     */
  .no-data {
    font-size: 14px;
  }
}
