/*   */
.navbar {
  background-color: transparent; /*    */
  display: flex;
  justify-content: flex-end; /*   */
  padding: 10px 20px;
  position: fixed; /*    */
  top: 0; /*   */
  right: 0; /*   */
  width: 100%; /*   100% */
  z-index: 1000; /*       */
}

/*    */
.logout-button {
  background-color: #ff4d4d;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.3s ease;
}

.logout-button:hover {
  background-color: #cc0000;
}
