/*    */
.profile-detail-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f4f7fc;
  padding: 40px;
  min-height: 100vh;
}

/*    */
.profile-card {
  margin-top: 20px;
  background-color: white;
  border-radius: 16px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 700px; /*       */
  padding: 30px;
  margin-bottom: 40px;
  transition: transform 0.2s ease-in-out;
}

.profile-card:hover {
  transform: translateY(-5px);
}

/*   (, , URL  ) */
.profile-header {
  display: grid;
  grid-template-columns: 20% 80%; /*  20%,  80%  */
  align-items: center;
  gap: 20px; /*   */
  position: relative;
}
.profile-left {
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-image {
  margin-top: 25px;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
  border: 4px solid #007bff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.profile-right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

.profile-info h2 {
  font-size: 1.6rem;
  font-weight: bold;
  color: #333;
}
/*  /  */
.follow-info {
  display: flex;
  justify-content: center;
  gap: 20px;
  font-size: 1.2rem;
  font-weight: bold;
  color: #333;
}

.copy-url-btn {
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: #007bff;
  transition: color 0.3s ease-in-out;
  position: absolute;
  top: 26px; /*   */
  right: 10px; /*   */
}

.copy-url-btn:hover {
  color: #0056b3;
}

/*       */
.profile-content-container {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 25px; /*     */
  position: relative;
  max-width: 900px; /*       */
  width: 100%;
}

.edit-btn {
  padding: 6px 12px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 50%;
  font-size: 1.5rem;
  cursor: pointer;
  position: absolute;
  top: 5px;
  right: 20px;
}

.edit-btn:hover {
  background-color: #0056b3;
}

/*   */
.profile-content {
  width: 100%;
  display: flex;
  justify-content: flex-start; /*   */
}

.profile-content p {
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 15px;
  padding: 10px;
  border-radius: 8px;
  white-space: pre-line; /*   */
  text-align: left; /*   */
  width: 100%; /*      */
}

.profile-content textarea {
  width: 80%; /*    */
  padding: 15px;
  font-size: 1.2rem;
  border-radius: 12px;
  border: 1px solid #ccc;
  min-height: 150px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  resize: none;
  transition: border-color 0.3s ease;
}

.profile-content textarea:focus {
  border-color: #007bff;
  outline: none;
}

/*  /   */
.edit-buttons {
  display: flex;
  justify-content: center;
  gap: 25px;
  margin-top: 20px;
  width: 100%;
  margin-bottom: 10px; /*     */
}

.save-btn,
.cancel-btn {
  padding: 12px 25px;
  font-size: 1.2rem;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background 0.3s ease-in-out;
}

.save-btn {
  background-color: #007bff;
}

.save-btn:hover {
  background-color: #0056b3;
}

.cancel-btn {
  background-color: #dc3545;
}

.cancel-btn:hover {
  background-color: #c82333;
}
/*    */
.tabs-container {
  display: flex;
  justify-content: center;
  margin-top: 40px;
  width: 100%; /*   100%  */
  border-radius: 10px 10px 0 0; /*    */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/*    */
.tab {
  flex: 1; /*       */
  padding: 12px 0; /*       */
  font-size: 1.1rem;
  cursor: pointer;
  border: none;
  background: #e9ecef;
  text-align: center; /*    */
  border-radius: 10px 10px 0 0; /*    */
  transition: background 0.3s ease-in-out, transform 0.2s ease-in-out;
}

.tab.active {
  background-color: #007bff;
  color: white;
}

/*    */
.tabs-content {
  margin-top: -1px; /*       margin-top  */
  padding: 20px;
  background: white;
  border-radius: 0 0 10px 10px; /*    */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/* Skill List */
.skill-container {
  margin-top: 30px;
  padding: 20px;
  font-size: 20px;
}

.skill-container h3 {
  margin-bottom: 15px;
}
