.backButton {
  display: block;
  padding: 10px 15px;
  background-color: #6c757d;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-bottom: 20px;
}

/*   */
.container {
  max-width: 800px;
  margin: auto;
  padding: 20px;
  text-align: center;
  margin-top: 4px; /*          */
}

/*   */
.postContainer {
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 10px;
  background-color: #f9f9f9;
  text-align: left;
}
/*   */
.postMeta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  font-size: 14px;
  color: #555;
}

.postMetaLeft,
.postMetaRight {
  display: flex;
  gap: 20px;
}

.postId,
.postDate,
.viewsContainer,
.commentCount {
  margin: 0;
  font-size: 14px;
  color: #555;
