/*   */
.postMeta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  font-size: 14px;
  color: #555;
}

/*   (,  ) */
.postMetaLeft {
  display: flex;
  gap: 20px;
  align-items: center;
}

/*   (,  ) */
.postMetaRight {
  display: flex;
  gap: 20px;
  align-items: center;
}

/*  */
.postId {
  margin: 0;
  font-size: 14px; /*    */
  color: #555;
  line-height: 1.6;
  vertical-align: middle; /*   */
}

/*   */
.postDate {
  margin: 0;
  font-size: 14px; /*    */
  color: #555;
  line-height: 1.6;
  vertical-align: middle; /*   */
  display: inline-block;
  transform: translateY(-5px); /*  2px  */
}

/*  */
.viewsContainer,
.commentCount {
  margin: 0;
  font-size: 14px; /*    */
  color: #555;
  line-height: 1.6;
}

/*        */
.titleAndAuthor {
  display: flex;
  justify-content: space-between;
  align-items: center; /*    */
  margin-bottom: 10px;
}

.title {
  font-size: 24px;
  font-weight: bold;
}

.authorInfo {
  font-size: 14px;
  color: #555;
  margin-left: 20px; /*    */
}

/*  */
.content {
  font-size: 16px;
  line-height: 1.6;
}

/*    */
.postDate {
  font-size: 14px;
  color: gray;
  margin-top: 10px;
}
/*   */
.likesContainer {
  display: flex;
  flex-direction: column; /*    */
  align-items: center; /*     */
  margin-top: 20px;
}

/*   */
.likesText {
  margin-bottom: 10px; /*     */
  font-size: 16px;
  font-weight: bold;
}

/*     */
.likesButtons {
  display: flex; /*    */
  justify-content: center; /*     */
  gap: 10px; /*     */
}

/*   */
.likeButton {
  padding: 5px 10px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

/*    */
.unlikeButton {
  padding: 5px 10px;
  background-color: #dc3545;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

/*    */
.codeContainer {
  margin-top: 20px;
}

/*   */
.codeBlock {
  background-color: #272822;
  color: white;
  padding: 10px;
  border-radius: 5px;
  margin-top: 10px;
}

/*   */
.code {
  font-family: monospace;
  white-space: pre-wrap;
  word-break: break-word;
}

/*   */
.editButton {
  margin-top: 20px;
  padding: 10px 15px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  display: block;
  width: 100%;
}

/*   */
.deleteButton {
  margin-top: 20px;
  padding: 10px 15px;
  background-color: #dc3545;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  display: block;
  width: 100%;
}

/*   */
.commentSection {
  margin-top: 30px;
  text-align: left;
}

/*   */
.commentList {
  list-style: none;
  padding: 0;
}

/*   */
.commentItem {
  padding: 10px;
  border-bottom: 1px solid #ddd;
}

/*   */
.commentDate {
  font-size: 0.9em;
  color: gray;
}

/*    */
.noComment {
  padding: 10px;
  text-align: center;
  color: gray;
}

/*   */
.commentInput {
  width: 100%;
  height: 100px;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  resize: none;
  box-sizing: border-box;
}

/*    */
.commentButton {
  margin-top: 10px;
  padding: 10px 15px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  display: block;
  width: 100%;
}

/*   */
.loading {
  text-align: center;
  font-size: 18px;
  margin-top: 50px;
}

/*       */
.commentActions {
  display: flex;
  gap: 10px;
  margin-top: 10px;
}

/*    */
.editCommentButton {
  padding: 5px 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

/*    */
.deleteCommentButton {
  padding: 5px 10px;
  background-color: #dc3545;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

/*     */
.replyCommentButton {
  padding: 5px 10px;
  background-color: #17a2b8;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-left: 10px;
}

/*    */
.replyForm {
  margin-top: 10px;
  padding-left: 20px;
}

/*    */
.replyInput {
  width: 100%;
  height: 80px;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  resize: none;
  box-sizing: border-box;
}

/*    */
.replyButton {
  margin-top: 10px;
  padding: 10px 15px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  display: block;
  width: 100%;
}

/*   */
.replyList {
  list-style: none;
  padding: 0;
  margin-top: 10px;
}

/*   */
.replyItem {
  padding: 10px;
  border-left: 2px solid #28a745;
  margin-left: 20px;
}

/*   */
.replyDate {
  font-size: 0.9em;
  color: gray;
}

/*       */
.replyActions {
  display: flex;
  gap: 10px;
  margin-top: 10px;
}

/*    */
.editReplyButton {
  padding: 5px 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

/*    */
.deleteReplyButton {
  padding: 5px 10px;
  background-color: #dc3545;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
