.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /*     */
}

.modal-container {
  position: relative; /*    */
  background: white;
  border-radius: 8px;
  box-shadow: inset;
  padding: 20px;
  width: 80%; /*   */
  max-width: 600px; /*    */
  max-height: 80%; /*    */
  overflow: hidden; /*       */
  display: flex;
  flex-direction: column;
}

.terms-scroll-box {
  max-height: 400px; /*     */
  overflow-y: auto; /*    */
  padding: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-bottom: 20px; /*     */
}

.modal-close {
  position: absolute;
  width: 20px;
  height: 20px;
  right: 5px;
  top: 5px;
  border: none;
  background: transparent;
  font-size: 16px;
  font-weight: bold;
  color: #333;
  cursor: pointer;
}

.modal-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.agree-button,
.cancel-button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: #4caf50; /*    */
  color: white;
}

.cancel-button {
  background-color: #f44336; /*    */
}
